import React from 'react';
import { Table, Text } from '../common';
import {
  FeeTablesFootNotes,
  MessagingPricingTableContainer,
  PricingTable,
} from './styled';
import { Link } from 'gatsby-link';
import { InternalLinks } from '../../utils/links';
import data from '../../../data/Pricing.json';
import { PricingCategory, PricingItem } from '../../types';
import { round } from '../../utils/round';

const MessagingPricingTable = () => {
  const pricingData = JSON.parse(data.code) as unknown as PricingCategory[];
  const TELGORITHM_10DLC_MMS_SENT = pricingData.find(
    (category) => category.categoryName === 'TELGORITHM_10DLC_MMS_SENT',
  )?.pricingItems as PricingItem[];

  const TELGORITHM_10DLC_MMS_RECEIVED = pricingData.find(
    (category) => category.categoryName === 'TELGORITHM_10DLC_MMS_RECEIVED',
  )?.pricingItems as PricingItem[];

  const TELGORITHM_10DLC_SMS_SENT = pricingData.find(
    (category) => category.categoryName === 'TELGORITHM_10DLC_SMS_SENT',
  )?.pricingItems as PricingItem[];

  const TELGORITHM_10DLC_SMS_RECEIVED = pricingData.find(
    (category) => category.categoryName === 'TELGORITHM_10DLC_SMS_RECEIVED',
  )?.pricingItems as PricingItem[];

  const TELGORITHM_TOLLFREE_MMS_SENT = pricingData.find(
    (category) => category.categoryName === 'TELGORITHM_TOLLFREE_MMS_SENT',
  )?.pricingItems as PricingItem[];

  const TELGORITHM_TOLLFREE_MMS_RECEIVED = pricingData.find(
    (category) => category.categoryName === 'TELGORITHM_TOLLFREE_MMS_RECEIVED',
  )?.pricingItems as PricingItem[];

  const TELGORITHM_TOLLFREE_SMS_SENT = pricingData.find(
    (category) => category.categoryName === 'TELGORITHM_TOLLFREE_SMS_SENT',
  )?.pricingItems as PricingItem[];

  const TELGORITHM_TOLLFREE_SMS_RECEIVED = pricingData.find(
    (category) => category.categoryName === 'TELGORITHM_TOLLFREE_SMS_RECEIVED',
  )?.pricingItems as PricingItem[];

  const renderPrice = (price: number, unitName: string) => {
    return price === 0 ? (
      <Text.Regular.Bold.md>Free</Text.Regular.Bold.md>
    ) : (
      <Text.Regular.Normal.md>{`$${round(price, 5).toFixed(
        5,
      )} / ${unitName}`}</Text.Regular.Normal.md>
    );
  };

  return (
    <MessagingPricingTableContainer>
      <Table.Container>
        <Table.Wrapper>
          <PricingTable>
            <Table>
              <Table.Header>
                <Table.Row>
                  <Table.HeaderCell>
                    <Text.Title.Bold.sm>Phone Number Types</Text.Title.Bold.sm>
                  </Table.HeaderCell>
                  <Table.HeaderCell>
                    <div>
                      <Text.Title.Bold.sm>SMS Inbound</Text.Title.Bold.sm>
                      <Text.Regular.Normal.sm>Received</Text.Regular.Normal.sm>
                    </div>
                  </Table.HeaderCell>
                  <Table.HeaderCell>
                    <div>
                      <Text.Title.Bold.sm>SMS Outbound</Text.Title.Bold.sm>
                      <Text.Regular.Normal.sm>Sent</Text.Regular.Normal.sm>
                    </div>
                  </Table.HeaderCell>
                  <Table.HeaderCell>
                    <div>
                      <Text.Title.Bold.sm>MMS Inbound</Text.Title.Bold.sm>
                      <Text.Regular.Normal.sm>Received</Text.Regular.Normal.sm>
                    </div>
                  </Table.HeaderCell>
                  <Table.HeaderCell>
                    <div>
                      <Text.Title.Bold.sm>MMS Outbound</Text.Title.Bold.sm>
                      <Text.Regular.Normal.sm>Sent</Text.Regular.Normal.sm>
                    </div>
                  </Table.HeaderCell>
                </Table.Row>
              </Table.Header>
              <Table.Body>
                <Table.Row>
                  <Table.BodyCell>
                    <Text.Regular.Normal.md>
                      10-digit Long Code (10DLC)
                    </Text.Regular.Normal.md>
                  </Table.BodyCell>
                  <Table.BodyCell>
                    {renderPrice(TELGORITHM_10DLC_SMS_RECEIVED[0].price, 'SMS')}
                  </Table.BodyCell>
                  <Table.BodyCell>
                    {renderPrice(TELGORITHM_10DLC_SMS_SENT[0].price, 'SMS')}
                  </Table.BodyCell>
                  <Table.BodyCell>
                    {renderPrice(TELGORITHM_10DLC_MMS_RECEIVED[0].price, 'MMS')}
                  </Table.BodyCell>
                  <Table.BodyCell>
                    {renderPrice(TELGORITHM_10DLC_MMS_SENT[0].price, 'MMS')}
                  </Table.BodyCell>
                </Table.Row>
                <Table.Row>
                  <Table.BodyCell>
                    <Text.Regular.Normal.md>Toll-free</Text.Regular.Normal.md>
                  </Table.BodyCell>
                  <Table.BodyCell>
                    {renderPrice(TELGORITHM_TOLLFREE_SMS_RECEIVED[0].price, 'SMS')}
                  </Table.BodyCell>
                  <Table.BodyCell>
                    {renderPrice(TELGORITHM_TOLLFREE_SMS_SENT[0].price, 'SMS')}
                  </Table.BodyCell>
                  <Table.BodyCell>
                    {renderPrice(TELGORITHM_TOLLFREE_MMS_RECEIVED[0].price, 'MMS')}
                  </Table.BodyCell>
                  <Table.BodyCell>
                    {renderPrice(TELGORITHM_TOLLFREE_MMS_SENT[0].price, 'MMS')}
                  </Table.BodyCell>
                </Table.Row>
              </Table.Body>
            </Table>
          </PricingTable>
        </Table.Wrapper>
      </Table.Container>
      <FeeTablesFootNotes>
        <p>
          *SMS / MMS messages are charged per segment. Prices do not include{' '}
          <Link to={InternalLinks.CarrierFees}>Carrier pass-through fees.</Link>
        </p>
      </FeeTablesFootNotes>
    </MessagingPricingTableContainer>
  );
};

export default MessagingPricingTable;
